.footer {
    background-color: #0a101e;
    min-height: 20vh;
}

.container {
    border: 1px solid black;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.logos {
    border: 1px solid black;
    height:  60px;
    width: 420px;
    display: flex;
    justify-content: space-between;
}

.socialLogo {
    width: 60px;
    height: 60px;
    background-color: darkgray;
}

@media screen and (max-width: 650px) {
    .logos {
        width: 100%;
    }
}